export default class Collection{
    _items = [];

    constructor(items = []){
        this._items = items;
    }

    all(){
        return this._items;
    }

    map(fn){
        return this._items.map(fn);
    }

    each(fn){
        return this._items.map(function(item){
            fn(item);
            return item;
        });
    }

    first(){
        return this._items.length > 0 ? this._items[0] : null;
    }
}



